
import { ShowHidePassword } from '../component/elements/ShowHidePassword';
import '../assets/css/pages/login.css';

function ResetPassword() {
    return (
        <section className="auth-form padding-60">
            <div className="container">
                <form>
                    <div className="form-box">
                        <div className="section-title varient-two">
                            <h4>Reset password</h4>
                            <p>Enter new password and then repeat it</p>
                        </div>
                        <div className="form-group">
                            <label htmlFor="new_password" className="form-label">New Password</label>
                            <ShowHidePassword  id="new_password" autocomplete="off" />
                        </div>
                        <div className="form-group">
                            <label htmlFor="repeat_new_password" className="form-label">Repeat Password</label>
                            <ShowHidePassword  id="repeat_new_password" autocomplete="off" />
                        </div>
                        <div className="row form-submit-btn">
                            <div className="col-lg-6">
                                <button className="button button-blue w-100" type="submit">Save</button>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </section>
    )
}

export default ResetPassword;