import { useState } from "react"

function ShowHidePassword(props) {

    const [passwordType, setPasswordType] = useState('password');

    const togglePassword = () => {
        if (passwordType === "password") {
            setPasswordType("text")
            return;
        }
        setPasswordType("password")
    }

    return (
        <div className="icon-group">
            <input type={passwordType} className="form-control" {...props} />
            <button className='password_toggle_btn' onClick={togglePassword} type="button">
                {
                    passwordType === "password" ? <i class="bi bi-eye"></i> : <i class="bi bi-eye-slash"></i>
                }
            </button>
        </div>
    )
}

export { ShowHidePassword }